<template>
  <div>
    <el-row :gutter="10">
      <p class="tip">调成英文输入法，鼠标定位到输入框中，开始用扫码枪扫码</p>
      <el-input v-model.number="orderId" @keyup.enter.native="search"
                clearable placeholder="订单号，调成英文输入法，鼠标点击此处，再用扫码枪" ref="inputRef"></el-input>
      <el-button type="primary" icon="el-icon-search" @click="search">查询</el-button>
      <el-button type="danger" icon="el-icon-circle-check" @click="orderPass">验票通过</el-button>
    </el-row>
    <hr>
    <table class="table" v-loading="loading">
      <tr>
        <td>入园时间</td>
        <td>{{order.enterTime}}</td>
      </tr>
      <tr>
        <td>预约日期</td>
        <td>{{order.reserveDate}}</td>
      </tr>
      <tr>
        <td>购买者手机</td>
        <td>{{order.mobile}}</td>
      </tr>
      <tr>
        <td>票总价/元</td>
        <td>{{order.totalPrice}}</td>
      </tr>
      <tr>
        <td>实际支付/元</td>
        <td>{{order.actualFee}}</td>
      </tr>
      <tr>
        <td>单价/元</td>
        <td>{{order.unit}}</td>
      </tr>
      <tr>
        <td>张数</td>
        <td>{{order.number}}</td>
      </tr>
      <tr>
        <td>优惠/元</td>
        <td>{{order.discountFee}}</td>
      </tr>
      <tr>
        <td>优惠券编号</td>
        <td>{{order.couponId}}</td>
      </tr>
      <tr>
        <td>订单号</td>
        <td>{{order.id}}</td>
      </tr>
      <tr>
        <td>支付状态</td>
        <td>{{order.status | formatStatus}}</td>
      </tr>
      <tr>
        <td>支付方式</td>
        <td>{{order.payWay | formatpayWay}}</td>
      </tr>
      <tr>
        <td>微信流水号</td>
        <td>{{order.payNum}}</td>
      </tr>
      <tr>
        <td>类型</td>
        <td>{{order.type | formatType}}</td>
      </tr>
      <tr>
        <td>二维码来源</td>
        <td>{{order.sourceName}}</td>
      </tr>
      <tr>
        <td>来源者手机</td>
        <td>{{order.sourceMobile}}</td>
      </tr>
      <tr>
        <td>创建日期</td>
        <td>{{order.time}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
  export default {
    name: 'OrderList',
    data() {
      return {
        orderId: '',
        loading: false,
        order: {}
      }
    },
    filters: {
      formatStatus: function (payStatus) {
        let text = ''
        let status = payStatus
        if (status === 1) {
          text = '未支付'
        } else if (status === 5) {
          text = '支付中'
        } else if (status === 10) {
          text = '支付成功'
        } else if (status === 15) {
          text = '支付失败'
        } else if (status === 20) {
          text = '已申请退款'
        } else if (status === 25) {
          text = '退款成功'
        } else if (status === 26) {
          text = '退款失败'
        } else if (status === 30) {
          text = '失效'
        } else if (status === 35) {
          text = '实际支付金额不对'
        }
        return text
      },
      formatType: function (type) {
        let text = ''
        if (type === 1) {
          text = '线上'
        } else if (type === 2) {
          text = '线下'
        }
        return text
      },
      formatTicketType: function (type) {
        let text = ''
        if (type === 1) {
          text = '团体购票'
        } else {
          text = '个体票'
        }
        return text
      },
      formatpayWay: function (type) {
        let text = ''
        if (type === 1) {
          text = '预充值'
        } else if (type === 3) {
          text = '微信'
        } else if (type === 5) {
          text = '现金'
        } else if (type === 7) {
          text = '支付宝'
        }
        return text
      }
    },
    methods: {
      search() {
        if (this.orderId) {
          let params = {orderId: this.orderId}
          this.query(this, params)
        } else {
          console.log('orderId为空', this.orderId)
        }
      },
      query(that, params) {
        console.log(params)
        that.loading = true
        that.$axios.get('/admin/order/id', {params: params}).then(function (res) {
          console.log(res.data)
          that.loading = false
          if (res.status === 200 && res.data.code === 200) {
            if (res.data.data && res.data.data.length > 0) {
              that.order = res.data.data[0]
            } else {
              that.order = {}
              that.$message({type: 'error', message: '未查到信息'})
            }
          } else {
            that.order = {}
            that.$message({type: 'error', message: res.data.msg})
          }
        }).catch((error) => {
          console.log(`查询err: ${error}`)
          that.loading = false
          that.order = {}
          that.$message({type: 'error', message: error})
        })
      },
      orderPass() {
        let that = this
        if (that.order.enterTime) {
          that.$message({type: 'error', message: '已入园'})
          return
        }
        if (that.order.id) {
          let params = {orderId: that.order.id}
          that.$axios.get('/admin/order/pass', {params: params}).then(function (res) {
            if (res.status === 200 && res.data.code === 200) {
              that.$message({type: 'success', message: '验票成功'})
              that.order = {}
              that.orderId = ''
              that.$refs.inputRef.$el.children[0].focus()
            } else {
              that.$message({type: 'error', message: res.data.msg || '后台出错'})
            }
          }).catch((error) => {
            console.log(`查询err: ${error}`)
            that.$message({type: 'error', message: error || '后台出错'})
          })
        } else {
          that.$refs.inputRef.$el.children[0].focus()
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-input {
    display: inline-block;
    width: 400px;
    font-size: 16px;
  }
  .tip{color: #ffaa55}
  .el-select {
    width: 100px;
  }

  .table {
    color: #444;
  }

  .table tr {
    padding: 15px;
  }

  .table td {
    width: 170px;
    padding: 8px 15px;
    border: 1px solid #ccc;
  }

  .table > tr:nth-child(odd) {
    background-color: #fafafa;
  }

</style>



