<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="130px" class="demo-ruleForm">
    <el-form-item label="归属景区" prop="placeId">
      <el-select v-model="ruleForm.placeId">
        <el-option
          v-for="item in placeOption"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="名称" prop="userId">
      <el-select
        v-model="ruleForm.userId"
        filterable
        remote
        reserve-keyword
        :disabled="setDisabled"
        placeholder="旅行社、旅游行业企业/从业者、司机，不填为散客"
        :remote-method="nameRemoteMethod"
        :loading="loading">
        <el-option
          v-for="item in travelOption"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="类型" prop="type">
      <el-select v-model="ruleForm.type" :disabled="setDisabled">
        <el-option
          v-for="item in optionsType"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="分类" prop="categoryId">
      <el-select v-model="ruleForm.categoryId" :disabled="setDisabled">
        <el-option
          v-for="item in optionsCategory"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="单价/元" prop="unit">
      <el-input v-model.number="ruleForm.unit"></el-input>
    </el-form-item>
    <el-form-item label="团购票最少人数" prop="people">
      <el-input v-model.number="ruleForm.minpeople" placeholder="此票价最少人数限制"></el-input>
    </el-form-item>
    <el-form-item label="团购票最多人数" prop="people">
      <el-input v-model.number="ruleForm.maxpeople" placeholder="此票价最多人数限制"></el-input>
    </el-form-item>
    <el-form-item label="状态" prop="status">
      <el-select v-model="ruleForm.status">
        <el-option
          v-for="item in optionsStatus"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    name: 'BillingAdd',
    data() {
      return {
        pageStatus: 'add', // 页面默认添加 add ,edit
        loading: false,
        ruleForm: {
          placeId: '',
          userId: '',
          type: 1,
          categoryId: 1,
          unit: 0,
          minpeople: '',
          maxpeople: '',
          status: 1
        },
        rules: {
          placeId: [{required: true, message: '请输入景区名称', trigger: 'blur'}],
          unit: [{required: true, message: '请输入单价', trigger: 'blur'}],
          categoryId: [{required: true, message: '请选择分类', trigger: 'blur'}],
          type: [{required: true, message: '请选择类型', trigger: 'blur'}],
          status: [{required: true, message: '请选择状态', trigger: 'blur'}]
        },
        id: '',
        setDisabled: false,
        placeOption: [],
        travelOption: [],
        optionsType: [{
          value: 1,
          label: '团体购票'
        }, {
          value: 2,
          label: '个体票'
        }],
        optionsStatus: [{
          value: 1,
          label: '启用'
        }, {
          value: 5,
          label: '禁用'
        }],
        optionsCategory: [{
          value: 1,
          label: '门票'
        }]
      }
    },
    beforeMount() {
      let that = this
      that.pageStatus = 'add'
      that.id = that.$route.params.id
      that.row = that.$route.params.row
      if (that.id) {
        that.pageStatus = 'edit'
        that.setDisabled = true
        that.ruleForm = that.row
        that.placeOption = [{value: that.row.placeId, label: that.row.placeName}]
        that.travelOption = [{value: that.row.userId, label: that.row.userName}]
      }
      that.placeRemoteMethod()
    },
    methods: {
      submitForm(formName) {
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let form = {
              id: that.id || '',
              placeId: that.ruleForm.placeId,
              userId: that.ruleForm.userId,
              type: that.ruleForm.type,
              categoryId: that.ruleForm.categoryId,
              unit: that.ruleForm.unit,
              minpeople: that.ruleForm.minpeople || 0,
              maxpeople: that.ruleForm.maxpeople || 0,
              status: that.ruleForm.status
            }
            console.log(form)
            let url = '/admin/billing/add'
            if (that.id) {
              url = '/admin/billing/edit'
            }
            that.$axios.post(url, form).then(function (res) {
              if (res.status === 200 && res.data.code === 200) {
                that.$message({type: 'success', message: '保存成功'})
                that.$router.push({name: 'BillingList'}) // 跳转列表页
              } else {
                that.$message({type: 'error', message: '添加失败'})
              }
            }).catch(function (err) {
              console.log('查询err:')
              console.log(err)
              that.$message({type: 'error', message: '保存失败'})
            })
          } else {
            that.$message({type: 'error', message: '保存失败'})
          }
        })
      },
      placeRemoteMethod() {
        let that = this
        let url = '/admin/place/getAll'
        that.$axios.get(url).then((res) => {
          if (res.data.code === 200) {
            console.log(res.data.data)
            let arr = res.data.data
            that.placeOption = arr.map(function (o) {
              return {value: o.id, label: o.name}
            })
            if (that.placeOption.length === 1) {
              that.ruleForm.placeId = that.placeOption[0].value
            }
          } else {
            console.log(res.data)
          }
        }).catch((error) => {
          console.log(error)
        })
      },
      nameRemoteMethod(travel) {
        console.log(travel)
        let that = this
        if (travel) {
          let url = '/admin/user/name/' + travel
          that.$axios.get(url).then((res) => {
            if (res.data.code === 200) {
              console.log(res.data.data)
              let arr = res.data.data
              that.travelOption = arr.map(function (o) {
                return {value: o.id, label: o.name + '_' + o.mobile}
              })
            } else {
              console.log(res.data)
            }
          }).catch((error) => {
            console.log(error)
          })
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-form {
    max-width: 550px;
  }

  .el-input, .el-select {
    width: 360px;
  }
</style>



